
import { defineComponent, ref } from 'vue';
import Input from './components/Input.vue'
export default defineComponent({
  name: 'App',
  components: {
    Input
  },
  setup() {
    const active = ref(false)
    const change = () => {
      active.value = !active.value;
    }

    return {
      active,
      change
    }
  }

});
