
import {defineComponent, ref, PropType, Prop } from 'vue'
import Todo from '../Types/Todo'


export default defineComponent({
     
    name:"ManagerTask",
    // props:['tasks','clear','valueFilterBy','deleteTask','changeTask','filter','filterBy'],
    props:{
        tasks:{
            type:Array as PropType<Todo[]>,
            required:false,
            default:null
        },
        clear:{
            type:Function,
            require:true,
        },
        valueFilterBy:{
            type:Function,
            required:true,
        },
        deleteTask:{
            type:Function,
            required:true,
        },
        changeTask:{
            type:Function,
            required:true,
        },
        filter:{
            type:Array as PropType<Todo[]>,
            required:false,
            default:null
        },
        filterBy:{
            type:[String,Boolean] as PropType<string|boolean>,
            required:true
        }
    },
    setup(props) {
        const taskss = ref(props.tasks)
      return {
          
          taskss,
      }
    },
    data() {
        return {
            data:this.filter
        }
    }

})
